.form {
  padding: 0 4px;
  legend {
    color: white;
  }

  &__title {
    text-align: center;
    color: white;
    display: flex;
    justify-content: center;
  }

  &__input {
    margin: 8px 0;
    padding: 8px;

    &-id {
      text-align: center;
      outline: none;
      border: none;
      margin-left: 5px;
      width: 50px;
      border-radius: 8px;
    }

    &-street {
      width: 100%;
      background-color: white;
    }

    &-phone {
      width: 100%;
      text-align: center;
      font-weight: bold;
      background-color: white;
    }

    &-numbers {
      min-height: 100px;
      display: grid;
      gap: 4px;
      grid-template-columns: repeat(2, 1fr);

      &__box {
        display: flex;
        gap: 8px;
        flex-direction: column;
        align-items: center;

        input {
          background-color: white;
        }

        span {
          color: white;
        }
      }
    }

    &-number {
      text-align: center;
      padding: 8px 0;
      width: 100%;
    }
  }
}

.payment {
  margin: 10px 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px;

  &__box {
    border: 2px solid white;
    background-color: rgba(0, 0, 0, 0.24);
    color: white;
    display: flex;
    flex-direction: column;
    gap: 4px;
    justify-content: space-around;
    align-items: center;

    input {
      width: 90px;
      text-align: center;
      background-color: white;
    }

    select {
      outline: none;
      border: none;
      width: 90px;
    }

    button {
      padding: 8px 16px;
    }
  }
}